import React, { useState } from "react";
import toast, { Toaster } from "react-hot-toast";
import { motion } from 'framer-motion';
import Button from "../Button/Button";
import services from "../../data/services.json";
import {animation} from "../../data/animation";
import * as API from "../../services/api";
import useLocalStorage from "../../hooks/useLocalStorage";
import icons from '../../assets/icons/icons.svg';
import css from "./EglamedForm.module.css";

const LS_KEY='eglcall_form';

const EglamedForm = () => {
    const initialValues = {name: "", surname: "", phone: "", eglservice: "", comment: "", egleglpolicy: false};

    const [data, setData] = useLocalStorage(LS_KEY, initialValues);
    const {name, surname, phone, eglservice, comment, eglpolicy}=data;
    const [isClickBut, setIsClickBut]=useState(false);
    const [isFetchOk, setIsFetchOk]=useState(false);
   
    // localStorage  next.js  -  ,   useEffect
    // useEffect(() => {
    //     const storedData = JSON.parse(window.localStorage.getItem(LS_KEY)) || initialValues;
    //     setData(storedData);
    // }, [setData]);
   
    const handleChange = (evt) => {
        const { name, value, type, checked } = evt.target;   
        setData(prevState=>({...prevState, [name]: type === "checkbox" ? checked : value}));   
    };
  

    const handleSubmit = (e) => {
        e.preventDefault();
  
        if (eglservice === "") {
            toast.error(`    `, {
                duration: 3000,
                position: "top-center",
            });
        } else 
        if (!eglpolicy) {
            toast.error(
                ` "V"   "   "`,
                { duration: 3000, position: "top-center" }
            );
        } else {
            const message = `': ${name}\n: ${surname}\n: ${phone}\n: ${eglservice}\n: ${comment}`;
            
            setIsClickBut(true);
    
            API.sendMessageToTelegram(message)
            .then(response=>{
                setTimeout(() => {
                    setIsClickBut(false);
                }, 500);
            
                setIsFetchOk(true);
                setTimeout(() => {
                    setIsFetchOk(false);
                 }, 4000);
                
            })
            .catch(error=>{
                const errorMessage="!     :(      .\n";
                toast.error(errorMessage);
                
                setIsClickBut(false);
              })
            .finally(()=> {
                setData(initialValues);              
            });
        }
    };

    return(
        <section id="callform" className={css.callform}>
            <div className="container">
                <motion.div 
                    className={css.wrapper}

                    initial="hide"
                    whileInView="show"
                    viewport={{ once: true }}
                    variants={animation}>

                    <Toaster />
                    <form className={css.formCall} onSubmit={handleSubmit} id="callform">
                        <h3 className={css.formTitle}>
                              ,
                            <br />   
                        </h3>
                        <label className={css.formLabel}>
                            <input
                                type="text"
                                className={css.formInput}
                                name="name"
                                placeholder="  '"
                                minLength="2"
                                pattern="[A-Za-z--'\-]{2,50}"
                                title="'        ',    2  50 "
                                required
                                value={name}
                                onChange={handleChange}

                            />
                        </label>

                        <label className={css.formLabel}>
                            <input
                                type="text"
                                className={css.formInput}
                                name="surname"
                                placeholder="  "
                                required
                                minLength="2"
                                pattern="[A-Za-z--'\-]{2,70}"
                                title="    , '-',  ',    2  70 "
                                value={surname}
                                onChange={handleChange}
                            />
                        </label>
                        <label htmlFor="eglservice" className={css.formLabel}>
                            <select
                                id="eglservice"
                                name="eglservice"
                                className={css.formSelect}
                                value={eglservice}
                                onChange={handleChange}
                
                            >
                                <option value="" disabled>
                                     
                                </option>
                                {services?.length > 0 &&
                                    services.map(({ id, title }) => (
                                        <option key={id} value={title}>
                                            {title}
                                        </option>
                                    ))}
                            </select>
                        </label>
                        <label className={css.formLabel}>
                            <input
                                type="tel"
                                className={css.formInput}
                                name="phone"
                                pattern="[+]{1}[0-9]{12}"
                                title="     '+'   12 "
                                placeholder="  +380XXXXXXXXX"
                                required
                                value={phone}
                                onChange={handleChange}
                            />
                        </label>

                        <label htmlFor="comment">
                            <textarea
                                className={css.formTextarea}
                                name="comment"
                                id="comment"
                                placeholder="  "
                                value={comment}
                                onChange={handleChange}
                            />
                        </label>
                        <div className={css.butWrapper}>
                            <div className={css.butWrap}>
                            <Button type="submit" caption="" isClickBut={isClickBut} isFetchOk={isFetchOk} />
                            </div>
                        </div>

                        <div className={css.policyWrapper}>
                            <input
                                type="checkbox"
                                name="eglpolicy"
                                id="eglpolicy"
                                className={css.checkboxPolicy}
                                checked={eglpolicy}
                                onChange={handleChange}
                            />
                            <svg width="16" height="15" className={css.checkboxIcon}>
                                <use href={`${icons}#icon-check`}></use>
                            </svg>
                            <label htmlFor="eglpolicy" className={css.policyLabel}>
                                 
                                <a
                                    href="./Privatepolicy.docx"
                                    className={css.policyLink}
                                >
                                    {" "}
                                     
                                </a>
                            </label>
                        </div>
                    </form>
                {isFetchOk && <p className={css.text}>  </p>}
                </motion.div>
            </div>
        </section>
    );
};

export default EglamedForm;
