import React from "react";
import toast, { Toaster } from "react-hot-toast";
import Button from "../Button/Button";
import services from "../../data/services";
import icons from '../../assets/icons/icons.svg';
import css from "../CallForm/CallForm.module.css";

const Form2 = () => {
    const handleSubmit = (e) => {
        e.preventDefault();
        const form = e.target;
        const formData = new FormData(form);
        const formJson = Object.fromEntries(formData.entries());
        const {name, surname, phone,servicetwo,commenttwo, policytwo}=formJson;

        if (servicetwo === "") {
            toast.error(`    `, {
                duration: 3000,
                position: "top-center",
            });
        } else if (policytwo!=="on") {
            toast.error(
                ` "V"   "   "`,
                { duration: 3000, position: "top-center" }
            );
        } else {
            console.log(
                `': ${name}\n: ${surname}\n: ${phone}\n: ${servicetwo}\n: ${commenttwo}`
            );

            form.reset();
        }
    };

    return (
        <div className={css.wrapper}>
            <Toaster />
            <h2>  FormData</h2>
            <form className={css.formCall} onSubmit={handleSubmit}>
                <h3 className={css.formTitle}>
                      ,
                    <br />   
                </h3>
                <label className={css.formLabel}>
                    <input
                        type="text"
                        className={css.formInput}
                        name="name"
                        placeholder="  '"
                        minLength="2"
                        pattern="[A-Za-z--'\-]{2,50}"
                        title="'        ',    2  50 "
                        autoFocus
                        required
                      
                    />
                </label>

                <label className={css.formLabel}>
                    <input
                        type="text"
                        className={css.formInput}
                        name="surname"
                        placeholder="  "
                        required
                        minLength="2"
                        pattern="[A-Za-z--'\-]{2,70}"
                        title="    , '-',  ',    2  70 "
                        
                    />
                </label>
                <label htmlFor="servicetwo" className={css.formLabel}>
                    <select
                        id="servicetwo"
                        name="servicetwo"
                        className={css.formSelect}
            
                 
                    >
                        <option value="">
                             
                        </option>
                        {services?.length > 0 &&
                            services.map(({ id, title }) => (
                                <option key={id} value={title}>
                                    {title}
                                </option>
                            ))}
                  
                    </select>
                </label>
                <label className={css.formLabel}>
                    <input
                        type="tel"
                        className={css.formInput}
                        name="phone"
                        pattern="[+]{1}[0-9]{12}"
                        title="     '+'   12 "
                        placeholder="  +380XXXXXXXXX"
                        required
                   
                        
                    />
                </label>

                <label htmlFor="commenttwo">
                    <textarea
                        className={css.formTextarea}
                        name="commenttwo"
                        id="commenttwo"
                        placeholder="  "
                 
                     
                    />
                </label>
                <div className={css.butWrapper}>
                    <Button type="submit" caption="" />
                </div>

                <div className={css.policyWrapper}>
                    <input
                        type="checkbox"
                        name="policytwo"
                        id="policytwo"
                        className={css.checkboxPolicy}
                        defaultChecked={false}
                    />
                    <svg width="16" height="15" className={css.checkboxIcon}>
                        <use href={`${icons}#icon-check`}></use>
                    </svg>
                    <label htmlFor="policytwo" className={css.policyLabel}>
                         
                        <a
                            href="/"
                            className={css.policyLink}
                        >
                            {" "}
                             
                        </a>
                    </label>
                </div>
            </form>
        </div>
    );
};

export default Form2;