import React from "react";
import toast, { Toaster } from "react-hot-toast";
import Button from "../Button/Button";
import services from "../../data/services";
import icons from '../../assets/icons/icons.svg';
import useLocalStorage from "hooks/useLocalStorage";
import css from "../CallForm/CallForm.module.css";

const LS_KEY='form';

const Form3 = () => {
    const initialValues = {name: "", surname: "", phone: "", servicethree: "", comment: "", policythree:false};
    const [data, setData] = useLocalStorage (LS_KEY, initialValues);
    const {name, surname, phone, servicethree, commentthree, policythree}=data;
    console.log("useLocalStorage data=", data);

    const handleChange = (evt) => {
        const { name, value, type, checked} = evt.target;    
        setData(prevState=>({...prevState, [name]: type === "checkbox" ? checked : value}));
        console.log("handleChange data=", data); 
     };

    const handleSubmit = (e) => {
        e.preventDefault();

        if (servicethree === "") {
            toast.error(`    `, {
                duration: 3000,
                position: "top-center",
            });
        } else if (!policythree) {
            toast.error(
                ` "V"   "   "`,
                { duration: 3000, position: "top-center" }
            );
        } else {
            console.log(
                `': ${name}\n: ${surname}\n: ${phone}\n: ${servicethree}\n: ${commentthree}`
            );

            setData({name: "", surname: "", phone: "", servicethree: "", commentthree: "", policythree: false});
           
        }
    };

    return (
        <div className={css.wrapper}>
            <Toaster />
            <h2>   useState</h2>
            <p> select  textarea   </p>
            <p>A component is changing a controlled input to be uncontrolled. This is likely caused by the value changing from a defined to undefined, which should not happen. Decide between using a controlled or uncontrolled input element for the lifetime of the component.</p>
            <form className={css.formCall} onSubmit={handleSubmit}>
                <h3 className={css.formTitle}>
                      ,
                    <br />   
                </h3>
                <label className={css.formLabel}>
                    <input
                        type="text"
                        className={css.formInput}
                        name="name"
                        placeholder="  '"
                        minLength="2"
                        pattern="[A-Za-z--'\-]{2,50}"
                        title="'        ',    2  50 "
                        autoFocus
                        required
                        value={name}
                        onChange={handleChange}
                    />
                </label>

                <label className={css.formLabel}>
                    <input
                        type="text"
                        className={css.formInput}
                        name="surname"
                        placeholder="  "
                        required
                        minLength="2"
                        pattern="[A-Za-z--'\-]{2,70}"
                        title="    , '-',  ',    2  70 "
                        value={surname}
                        onChange={handleChange}
                    />
                </label>
                <label htmlFor="servicethree" className={css.formLabel}>
                    <select
                        id="servicethree"
                        name="servicethree"
                        className={css.formSelect}
                        value={servicethree}
                        onChange={handleChange}
                    >
                        <option value="">
                             
                        </option>
                        {services?.length > 0 &&
                            services.map(({ id, title }) => (
                                <option key={id} value={title}>
                                    {title}
                                </option>
                            ))}
                    </select>
                </label>
                <label className={css.formLabel}>
                    <input
                        type="tel"
                        className={css.formInput}
                        name="phone"
                        pattern="[+]{1}[0-9]{12}"
                        title="     '+'   12 "
                        placeholder="  +380XXXXXXXXX"
                        required
                        value={phone}
                        onChange={handleChange}
                    />
                </label>

                <label htmlFor="commentthree">
                    <textarea
                        className={css.formTextarea}
                        name="commentthree"
                        id="commentthree"
                        placeholder="  "
                        value={commentthree}
                        onChange={handleChange}
                    />
                </label>
                <div className={css.butWrapper}>
                    <Button type="submit" caption="" />
                </div>

                <div className={css.policyWrapper}>
                    <input
                        type="checkbox"
                        name="policythree"
                        id="policythree"
                        className={css.checkboxPolicy}
                        checked={policythree}
                        onChange={handleChange}
                    />
                    <svg width="16" height="15" className={css.checkboxIcon}>
                        <use href={`${icons}#icon-check`}></use>
                    </svg>
                    <label htmlFor="policythree" className={css.policyLabel}>
                         
                        <a
                            href="./PrivatePolicy.docx"
                            className={css.policyLink}
                        >
                            {" "}
                             
                        </a>
                    </label>
                </div>
            </form>
        </div>
    );
};

export default Form3;
